/** @jsxRuntime classic */
/** @jsx jsx */
import { css, jsx } from 'theme-ui';
import {useContext} from 'react';
import {MovieContext} from '../../Context/MovieContext';
import Container from "../ReusableComponents/Container";

const Popular = () => {
    const {popularMovies} = useContext(MovieContext);
    
    const setVoteClass = (vote) => {
        if (vote >= 8) {
            return "green";
        } else if (vote >= 6) {
            return "orange";
        } else {
            return "red";
        }
    };

    return(
        <div className="movie-container">
            <Container>
        {popularMovies.results && popularMovies.results.map((popularMovieItem) => (
             <div key={popularMovieItem.index} className="movie">
             <img src={`https://image.tmdb.org/t/p/w400/${popularMovieItem.poster_path}`} alt="poster" />
             <div className="movie-info">
                 <h3>{popularMovieItem.title}</h3>
                 <span className={`tag ${setVoteClass(popularMovieItem.vote_average)}`}>{popularMovieItem.vote_average}</span>

             </div>
 
             <div className="movie-over">
                 <h2>Overview</h2>
                 <p>{popularMovieItem.overview}</p>
             </div>
         </div>
            // <img key={index} src={`https://image.tmdb.org/t/p/w400/${popularMovieItem.poster_path}`} alt="poster"/>
        ))}
            </Container>
        </div>
    );
};

