/** @jsxRuntime classic */
/** @jsx jsx */
import { css, jsx } from 'theme-ui';
import {useContext} from 'react';
import {MovieContext} from '../../Context/MovieContext';

const HeroNavlink = ({ btnText }) => {
    const { setHiddenMenu, activeLink, setActiveLink } = useContext(MovieContext);
    return <button onClick={() => {
        setActiveLink(btnText);
        setHiddenMenu(true);
    }} 
    css={styles} style={{ color: activeLink === btnText ? "#f9a5ff" : "#fff" }}>{btnText}</button>
};

const styles = css`
    border: none;
    outline: none;
    padding: 10px;
    background: transparent;
    text-align: center;
    font-size: 22px;
    margin: 24px;
    font-weight: 100;
    cursor: pointer;
    user-select: none;
    @media (max-width: 860px) {
        font-size: 20px;
    }
`;
export default HeroNavlink;