/** @jsxRuntime classic */
/** @jsx jsx */
import { css, jsx } from 'theme-ui';
import HeroNavlink from "./HeroNavlink";
import {useContext} from 'react';
import {MovieContext} from '../../Context/MovieContext';

const HeroNavMenu = () => {
    const { hiddenMenu } = useContext(MovieContext);
   
    return(
        <div css={styles} className={(hiddenMenu ? "hidden" : "") + " heroNavMenu"}>
            <HeroNavlink btnText="Popular" />
            <HeroNavlink  btnText="All Movies" />
        </div>
    );
};

const styles = css`
    padding-left: 80px;
    @media (max-width: 860px) {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 1;
        min-height: 11360px;
        background: rgba(0, 0, 0, 0.8);
        padding: 20px 50px;
        width: 100%;
        max-width: 340px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        z-index: 1;
        transition: all 800ms ease-in-out;
        &.hidden {
            left: -600px;
            opacity: 0;
        }
    }
`;
export default HeroNavMenu;